* {
  margin: 0;
  padding: 0;
}

html {
  --blue: #00acdc;
  --header-heigth: 60px;
  font-family: Roboto, serif;
  font-size: 12px;
  font-weight: 500;
}

header {
  text-transform: uppercase;
  height: var(--header-heigth);
  justify-content: space-between;
  padding: 0 50px;
  display: flex;
}

.logo {
  align-items: center;
  display: flex;
}

.tab-container {
  flex: row;
  height: 100%;
  display: flex;
}

.tab {
  justify-content: space-around;
  align-items: center;
  height: 100%;
  margin-left: 20px;
  display: flex;
  position: relative;
}

.tab:first-child {
  margin: 0;
}

.nav-link {
  color: #000;
  align-items: center;
  height: 100%;
  text-decoration: none;
  display: flex;
}

.nav-link:visited {
  color: #000;
}

.nav-link:hover, .is-active, .is-active:visited {
  color: var(--blue);
}

.is-active:after {
  content: "";
  top: calc(var(--header-heigth)  - 4px);
  background-color: var(--blue);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  left: -1px;
}

